import './styles.module.css';

import Moment from 'moment';
import { extendMoment } from 'moment-range';

const moment = extendMoment(Moment);
import { useEffect, useState } from 'react';

export const MonthView = ({ year, month, onCellClick }) => {
  const [startDate, setStartDate] = useState(moment([year, month]));
  const [firstDay, setFirstDay] = useState(moment([year, month]).startOf('month'));
  const [endDay, setEndDay] = useState(moment[(year, month)].endOf('moth'));
  const [monthRange, setMonthRange] = moment.range(firstDay, endDay);
  const [weeks, setWeeks] = useState(
    Array.from(
      moment
        .range(
          moment(year.concat('-').concat(month)).startOf('month'),
          moment(year.concat('-').concat(month)).endOf('month'),
        )
        .by('week'),
    ),
  );

  useEffect(() => {
    const startDate = moment(year.concat('-').concat(month)).startOf('month');
    const endDate = moment(year.concat('-').concat(month)).endOf('month');
    setWeeksRange(Array.from(moment.range(startDate, endDate).by('week')));
    Array.from(moment.range(startDate, endDate).by('week')).map(week => {
      var daysRange = Array.from(
        moment.range(week.startOf('week').toDate(), week.endOf('week').toDate()).by('day'),
      );
      console.log(daysRange.map(x => x.format()));
    });
    console.log(startDate.format());
    console.log(endDate.format());
    console.log(weeksRange.map(x => x.format()));
  }, [month, year]);

  return (
    <div className="table-responsive">
      <table className="table table-bordered">
        <thead>
          <tr>
            {moment.weekdays().map(weekDay => (
              <th key={weekDay} className="days-of-week">
                {weekDay}
              </th>
            ))}
          </tr>
        </thead>
        <tbody>
          {weeksRange.map(week => {
            var daysRange = Array.from(
              moment.range(week.startOf('week').toDate(), week.endOf('week').toDate()).by('day'),
            );

            return (
              <tr>
                {daysRange.map(d => (
                  <td
                    data-value={d.format()}
                    className={d.format('MM') != month ? 'text-secondary' : 'text-body'}
                    onClick={e => onCellClick(e.target.dataset.value)}
                  >
                    {d.format('DD')}
                  </td>
                ))}
              </tr>
            );
          })}
        </tbody>
      </table>
    </div>
  );
};
